:root {
  --font-inter:"Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 15%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text2);
}

@media (min-width: 71.875em) {
  h1 {
    font-size: 3rem;
  }
}

@media (min-width: 87.5em) {
  h1 {
    font-size: 3rem;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
  line-height: 1.1;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card {
  background: var(--dark-card);
  color: var(--dark-text2);
  width: 15.9375rem;
  border-radius: 0.3125rem;
  text-align: center;
  padding: 1.5625rem;
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: bold;
}
.card__count {
  color: white;
  font-size: 3.5rem;
  font-weight: bold;
  line-height: 1;
  letter-spacing: -0.125rem;
  margin-bottom: 0.25rem;
}
.card__label {
  font-size: 0.75rem;
  color: var(--dark-text1);
  font-weight: normal;
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
  font-size: 0.75rem;
  font-weight: bold;
  letter-spacing: 0.1875rem;
  letter-spacing: normal;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}

.toggle {
  border: none;
  display: grid;
  grid-template-columns: 1fr 3.75rem 1fr;
  grid-template-rows: repeat(2, 24px);
  gap: 0.3rem 0.8125rem;
}
.toggle__wrapper {
  height: 1.5rem;
  width: 4.5rem;
  grid-column: 2/3;
  grid-row: 1/2;
  position: relative;
  justify-self: center;
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  grid-row: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
  align-self: start;
}
.toggle label[for=light] {
  grid-column: 3/4;
  grid-row: 1/2;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -2px 0 -2px;
  cursor: pointer;
  height: 100%;
  width: 33.3333333333%;
  z-index: 1;
  position: relative;
}
.toggle__background {
  display: block;
  border-radius: 0.75rem;
  position: absolute;
  height: 100%;
  width: 100%;
  transition: all 0.1s ease-out;
  top: 0;
  left: 0;
  z-index: 0;
  overflow: hidden;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  pointer-events: none;
}
.toggle__button {
  display: block;
  position: absolute;
  border-radius: 50%;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  background-color: var(--dark-bg);
  transition: all 0.1s ease-out;
  z-index: 2;
}
.toggle #light:checked ~ .toggle__button {
  right: 0.1875rem;
  left: calc(100% - 21px);
}
.toggle #system:checked ~ .toggle__button {
  right: auto;
  left: 50%;
  transform: translate(-50%);
}

.header {
  display: flex;
  flex-wrap: wrap;
  gap: 0.625rem;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}